$blue: #92C7CF;
$beige: #C7B7A3;

.Button {
  display: inline-block;
  color: white;
  font-weight: bold;
  outline: none;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  height: 3rem;
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 1.25rem;

  &.code{
    background: $beige;
  }

  &.board{
    background: $blue;
  }

  &.board:hover {
    background: lighten($blue, 10%);
  }

  &.board:active{
    background: darken($blue, 10%);
  }

  &.code:hover {
    background: lighten($beige, 10%);
  }

  &.code:active{
    background: darken($beige, 10%);
  }

  & + & {
    margin-left: 1rem;
  }
}
